body {
  background-color: #6F9436;
  margin: 0;
  color: #212121;
}

ul, li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.main {
  background-color: #fff;
  width: 567px;
  margin: 100px auto;
  -webkit-box-shadow: 0 2px 4px 0;
          box-shadow: 0 2px 4px 0;
}

.header {
  line-height: 128px;
  font-size: 36px;
  font-family: 'Arial Rounded MT Bold';
  text-align: center;
}

.todo-list {

  height: 280px;
}
.todo-list .container {
  margin: 20px 120px;
  border: 1px solid green;
  height: 240px;
  overflow: auto;
}
.todo-list li {
  line-height: 60px;
  font-size: 24px;
}
.todo-list li.completed{
  text-decoration: line-through;
  color:#989191;
  position:relative;
}
.todo-list li.completed:after{
  content:' ';
  display: block;
  width: 30px;
  height:30px;
  background-size: cover;
  background-image: url('./img/checking.svg');
  position:absolute;
  right:0;
  top:10px;
}
.form {

  height: 110px;
}
.form .container {
  width: 400px;
  margin: 40px auto;
  position: relative;
}

.form .text-input {
  width: 360px;
  height: 40px;
  background-color: #D8D8D8;
  padding-left: 20px;
  font-size: 1.3em;
}

.form .text-input:focus {
  outline: none;
}

.form .submit{
  position: absolute;
  right: 0;
  top: -20px;
  border:none;
  background-color: #7Ed321;
  display: block;
  width: 80px;
  height:80px;
  border-radius: 50%;
  background-image: url(./img/add.svg);
  background-size: 40px;
  float: right;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-box-shadow: 0px 2px 4px;
          box-shadow: 0px 2px 4px;
  cursor: pointer;
}
.form .submit:focus {
  outline: none;
}
.actions {
  height: 110px;
}
.actions .container {
  width: 190px;
  margin: 20px auto;
}
.actions div {
  width: 50px;
  height: 50px;
  background-size: 40;
  background-position: center center;
  background-repeat: no-repeat;
  cursor: pointer;
}

.actions .list {
  float: left;
  background-image: url(./img/list.svg);
}

.actions .completed {
  float: right;
  background-image: url(./img/right-red.svg);
}



